import * as index from '../index.js';
const fetching = new Map();
export default class BaseUser {
    id;
    client;
    constructor(id, client) {
        this.id = id;
        this.client = client;
    }
    async fetch(noCache) {
        // Check if the user is in cache
        if (!noCache && this.client.cacheManager.users.has(this.id)) {
            const cached = this.client.cacheManager.users.get(this.id);
            return new index.User(cached.id, this.client, cached);
        }
        // Check if already fetching
        if (fetching.has(this.id))
            return new index.User(this.id, this.client, (await fetching.get(this.id)).data);
        // GET the user from server
        let request = this.client.sendHTTP(`/users/${this.id === -1 ? "@me" : this.id}`);
        fetching.set(this.id, request);
        let res = (await request).data;
        fetching.delete(this.id);
        // Update cache
        this.client.cacheManager.users.set(this.id, res);
        // Create the user
        const user = new index.User(res.id, this.client, res);
        // Return user
        return user;
    }
    async updateAvatar(newAvatar) {
        let data = (await this.client.sendHTTP(`/users/${this.id}/avatar`, "PATCH", {
            avatar: newAvatar
        })).data;
        return new index.User(data.id, this.client, data);
    }
}
